import Layout from "../components/MyLayout.js";
import axios from "axios";

class Index extends React.Component {
  state = {
    blog: [],
    front: []
  };

  componentDidMount() {
    axios.get(`static/content/blog.json`).then(res => {
      const blog = res.data;
      this.setState({ blog });
    });
  }

  render() {
 

 

  

    return (
      <Layout>
        <p>Home</p>

        {this.state.blog.main.map(item=> (

                <p> {item}</p>
        ))}
        
      </Layout>
    );
  }
}

export default Index;
